import './App.scss';

import postsFromServer from './api/posts.json';
import commentsFromServer from './api/comments.json';
import usersFromServer from './api/users.json';
import { PostList } from './components/PostList';

export const combinedPosts = postsFromServer.map((post) => {
  const newPost = { ...post };

  newPost.user = usersFromServer.find(user => user.id === post.userId);
  newPost.comments = commentsFromServer
    .filter(comment => post.id === comment.postId);

  return newPost;
});

export const App = () => (
  <section className="App">
    <h1 className="App__title">Static list of posts</h1>
    <PostList data={combinedPosts} />
  </section>
);
